import { useMemo } from "react";

import { IState } from "../types";
import { addNewAnswer, IAddNewAnswerAction } from "./reducers/addNewAnswer";
import { addNewGroupAnswer, IAddNewGroupAnswerAction } from "./reducers/addNewGroupAnswer";
import { IRemoveAnswerAction, removeAnswer } from "./reducers/removeAnswer";
import { IRemoveGroupAction, removeGroup } from "./reducers/removeGroup";
import {
  ISelectActiveDataPointAction,
  selectActiveDataPoint,
} from "./reducers/selectActiveDataPoint";
import { ISelectBlockAction, selectBlock } from "./reducers/selectBlock";
import { ISelectQuestionAction, selectNewQuestion } from "./reducers/selectNewQuestion";
import { IToggleGroupCollapseAction, toggleGroupCollapse } from "./reducers/toggleGroupCollapse";
import { IUpdateAnswerValueAction, updateAnswerValue } from "./reducers/updateAnswerValue";

type Action =
  | IAddNewAnswerAction
  | IAddNewGroupAnswerAction
  | IRemoveAnswerAction
  | IRemoveGroupAction
  | ISelectActiveDataPointAction
  | ISelectBlockAction
  | ISelectQuestionAction
  | IToggleGroupCollapseAction
  | IUpdateAnswerValueAction;

export const reducer = (state: IState, action: Action): IState => {
  switch (action.type) {
    case "ADD NEW ANSWER":
      return addNewAnswer(state, action);
    case "ADD NEW GROUP ANSWER":
      return addNewGroupAnswer(state, action);
    case "SELECT ACTIVE DATA POINT":
      return selectActiveDataPoint(state, action);
    case "SELECT BLOCK":
      return selectBlock(state, action);
    case "SELECT QUESTION":
      return selectNewQuestion(state, action);
    case "REMOVE ANSWER":
      return removeAnswer(state, action);
    case "REMOVE GROUP":
      return removeGroup(state, action);
    case "TOGGLE GROUP COLLAPSE":
      return toggleGroupCollapse(state, action);
    case "UPDATE ANSWER VALUE":
      return updateAnswerValue(state, action);
    default:
      console.warn("Unhandled action:", action);
      return state;
  }
};

type Dispatch = (action: Action) => void;
export interface IStateCallbacks {
  onAddNewAnswer: (groupIndex: number, groupKey: string, key: string) => void;
  onAddNewGroupAnswer: (groupKey: string) => void;
  onAddNewNonGroupedAnswer: (key: string) => void;
  onRemoveAnswer: (groupKey: string, groupIndex: number, key: string, index: number) => void;
  onRemoveGroup: (groupKey: string, groupIndex: number) => void;
  onRemoveNonGroupedAnswer: (key: string, index: number) => void;
  onSelectActiveDataPoint: (
    groupKey: string,
    groupIndex: number,
    key: string,
    index: number,
  ) => void;
  onSelectActiveNonGroupedDataPoint: (key: string, index: number) => void;
  onSelectBlock: (blockId: string) => void;
  onSelectDataPoint: (key: string) => void;
  onToggleGroupCollapse: (groupKey: string, groupIndex: number) => void;
  onUpdateGroupedAnswer: (
    groupKey: string,
    groupIndex: number,
    key: string,
    index: number,
    value: any,
    valueRaw: string,
  ) => void;
  onUpdateNonGroupedAnswer: (key: string, answer: number, value: any, valueRaw: string) => void;
}
export const useStateCallbacks = (dispatch: Dispatch) =>
  useMemo(
    (): IStateCallbacks => ({
      onAddNewAnswer: (groupIndex: number, groupKey: string, key: string) => {
        dispatch({
          groupIndex,
          groupKey,
          key,
          type: "ADD NEW ANSWER",
        });
      },
      onAddNewGroupAnswer: (groupKey: string) => {
        dispatch({
          groupKey,
          type: "ADD NEW GROUP ANSWER",
        });
      },
      onAddNewNonGroupedAnswer: (key: string) => {
        dispatch({
          groupIndex: -1,
          groupKey: key,
          key,
          type: "ADD NEW ANSWER",
        });
      },
      onRemoveAnswer: (groupKey: string, groupIndex: number, key: string, index: number) => {
        dispatch({
          groupIndex,
          groupKey,
          index,
          key,
          type: "REMOVE ANSWER",
        });
      },
      onRemoveGroup: (groupKey: string, groupIndex: number) => {
        dispatch({
          groupIndex,
          groupKey,
          type: "REMOVE GROUP",
        });
      },
      onRemoveNonGroupedAnswer: (key: string, index: number) => {
        dispatch({
          groupIndex: -1,
          groupKey: key,
          index,
          key,
          type: "REMOVE ANSWER",
        });
      },
      onSelectActiveDataPoint: (
        groupKey: string,
        groupIndex: number,
        key: string,
        index: number,
      ) => {
        dispatch({
          groupIndex,
          groupKey,
          index,
          key,
          type: "SELECT ACTIVE DATA POINT",
        });
      },
      onSelectActiveNonGroupedDataPoint: (key: string, index: number) => {
        dispatch({
          groupIndex: 0,
          groupKey: key,
          index,
          key,
          type: "SELECT ACTIVE DATA POINT",
        });
      },
      onSelectBlock: (idRef: string) => {
        dispatch({
          idRef,
          type: "SELECT BLOCK",
        });
      },
      onSelectDataPoint: (key: string) => {
        dispatch({
          key,
          type: "SELECT QUESTION",
        });
      },
      onToggleGroupCollapse: (groupKey: string, groupIndex: number) => {
        dispatch({
          groupIndex,
          groupKey,
          type: "TOGGLE GROUP COLLAPSE",
        });
      },
      onUpdateGroupedAnswer: (
        groupKey: string,
        groupIndex: number,
        key: string,
        index: number,
        value: any,
        valueRaw: string,
      ) => {
        dispatch({
          groupIndex,
          groupKey,
          index,
          key,
          type: "UPDATE ANSWER VALUE",
          value,
          valueRaw,
        });
      },
      onUpdateNonGroupedAnswer: (key: string, index: number, value: any, valueRaw: string) => {
        dispatch({
          groupIndex: index,
          groupKey: key,
          index,
          key,
          type: "UPDATE ANSWER VALUE",
          value,
          valueRaw,
        });
      },
    }),
    [dispatch],
  );



