import React from "react";
import { Button, ButtonGroup, ButtonToolbar } from "reactstrap";

interface IProps {
  aborting: boolean;
  canSave: boolean;
  onAbort: () => Promise<void>;
  onSave: () => Promise<void>;
  saving: boolean;
}

export const ActionToolbar = ({ aborting, onAbort, canSave, onSave, saving }: IProps) => {
  return (
    <ButtonToolbar className="actions d-flex justify-content-between p-2">
      <ButtonGroup>
        <Button color="secondary" disabled={aborting || saving} onClick={onAbort} type="button">
          Abort
        </Button>
      </ButtonGroup>
      <ButtonGroup>
        <Button
          color="primary"
          disabled={aborting || saving || !canSave}
          onClick={onSave}
          type="button"
        >
          Save
        </Button>
      </ButtonGroup>
    </ButtonToolbar>
  );
};
ActionToolbar.displayName = "ActionToolbar";



