import React, { useEffect, useState } from "react";
import { Alert } from "reactstrap";

import { IRenderJobOptions, Loading } from "@ilabs/ui";
import * as Constants from "../../constants";
import { Mapping } from "../Mapping";

interface IProps {
  readonly opts: IRenderJobOptions<string>;
}
interface IState {
  aborting: boolean;
  error: Error | null;
  saving: boolean;
  taxonomy: any;
}

export const Handler = ({ opts }: IProps) => {
  const [state, setState] = useState<IState>({
    aborting: false,
    error: null,
    saving: false,
    taxonomy: null,
  });
  useEffect(() => {
    const {
      state: { job },
    } = opts;
    if (!job) {
      return;
    }
    const taxonomyContents = job.contents.filter(c => c.role === "mapping-taxonomy-json");
    if (taxonomyContents.length < 1) {
      throw new Error("Unable to fetch taxonomy content.");
    }
    const fetchTaxonomy = async (contentId: string) => {
      try {
        let parsed = await opts.actions.fetchContent(contentId);
        if (typeof parsed === "string") {
          parsed = JSON.parse(parsed);
        }
        if (typeof parsed === "object") {
          setState({
            ...state,
            error: null,
            taxonomy: parsed,
          });
          return;
        }
        throw new Error("Invalid taxonomy result.");
      } catch (err) {
        setState({
          ...state,
          error: err,
          taxonomy: null,
        });
      }
    };
    fetchTaxonomy(taxonomyContents[0].id);
  }, [opts.state.job]);
  const { error } = state;
  if (!error) {
    if (!opts.state.job || !opts.state.contents || state.taxonomy === null) {
      return <Loading text="Loading..." />;
    }
    return (
      <Mapping
        aborting={state.aborting}
        onAbort={async () => {
          if (!opts.state.job) {
            return;
          }
          if (window.confirm("Are you sure you want to abort this task?")) {
            setState({
              ...state,
              aborting: true,
            });
            setTimeout(() => opts.actions.checkinJobAborted(), 0);
          }
        }}
        onSave={async (newXml: string) => {
          try {
            const { job } = opts.state;
            if (!job) {
              return;
            }
            setState({
              ...state,
              saving: true,
            });
            setTimeout(async () => {
              const jobOutput = job.contents.filter(c => c.role === Constants.OUTPUT_ROLE);
              if (jobOutput.length < 1) {
                throw new Error("Could not find output content.");
              }
              await opts.actions.saveContent(jobOutput[0].id, newXml);
              await opts.actions.checkinJobSuccess();
            }, 0);
          } catch (error) {
            setState({ aborting: false, error, saving: false, taxonomy: null });
          }
        }}
        saving={state.saving}
        rawTaxonomy={state.taxonomy}
        xml={opts.state.contents}
      />
    );
  }
  console.error(error);
  return <Alert color="danger">{error.message}</Alert>;
};
Handler.displayName = "Handler";



