import React, { memo } from "react";
import { ListGroup } from "reactstrap";

import { IStateCallbacks } from "../state";
import { IState, ITaxonomy } from "../types";
import { Question } from "./Question";

interface IProps {
  state: IState;
  stateCallbacks: IStateCallbacks;
  taxonomy: ITaxonomy;
}

export const Questions = memo(({ state, stateCallbacks, taxonomy }: IProps) => {
  const keys = Object.keys(state.dataPoints);
  return (
    <ListGroup className="h-100 overflow-auto border-top" flush>
      {keys.map((key, i) => {
        return (
          <Question
            active={state.activeDataPoint}
            dataPoint={state.dataPoints[key]}
            key={key}
            index={i + 1}
            stateCallbacks={stateCallbacks}
            taxonomy={taxonomy}
          />
        );
      })}
    </ListGroup>
  );
});
Questions.displayName = "Questions";



