import cx from "classnames";
import React from "react";

interface IProps {
  disabled: boolean;
  onClick: () => void;
}

export const RemoveButton = ({ disabled, onClick }: IProps) => (
  <a
    className={cx({ "text-muted": disabled, "text-danger": !disabled })}
    href="#"
    onClick={e => {
      e.preventDefault();
      if (!disabled) {
        if (window.confirm("Are you sure you want to remove this?")) {
          onClick();
        }
      }
    }}
    title="Remove"
  >
    <i className="fa fa-trash" />
  </a>
);
RemoveButton.displayName = "RemoveButton";



