import React from "react";

import { Workbench as GenericWorkbench } from "@ilabs/ui";
import { Handler } from "./Handler";

import * as Constants from "../../constants";

import "@ilabs/ui/styles/App.scss";
import "@ilabs/ui/styles/Sidebar.scss";

export const Workbench = () => (
  <GenericWorkbench<string>
    appTitle={Constants.APP_TITLE}
    basename={process.env.PUBLIC_URL || "/"}
    blockingTaskKind={Constants.TASK_KIND}
    roleWhitelist={[Constants.INPUT_ROLE]}
  >
    {opts => (
      <div className="h-100">
        <Handler opts={opts} />
      </div>
    )}
  </GenericWorkbench>
);
Workbench.displayName = "Workbench";



