import { IDataPoint, IDataPointGroup, IState } from "../../types";

export interface IRemoveAnswerAction {
  groupIndex: number;
  groupKey: string;
  index: number;
  key: string;
  type: "REMOVE ANSWER";
}
export const removeAnswer = (state: IState, action: IRemoveAnswerAction): IState => {
  if (action.groupKey in state.dataPoints) {
    if (state.dataPoints[action.groupKey].isGroup) {
      const group = state.dataPoints[action.groupKey] as IDataPointGroup;
      if (
        group.values[action.groupIndex] === undefined ||
        !(action.key in group.values[action.groupIndex]) ||
        group.values[action.groupIndex][action.key].length < 2 ||
        group.values[action.groupIndex][action.key][action.index] === undefined
      ) {
        return state;
      }
      return {
        ...state,
        activeDataPoint: {
          groupIndex: action.groupIndex,
          groupKey: action.groupKey,
          index:
            state.activeDataPoint.index >= action.index
              ? state.activeDataPoint.index - 1
              : state.activeDataPoint.index,
          key: action.key,
        },
        dataPoints: {
          ...state.dataPoints,
          [action.groupKey]: {
            ...group,
            values: [
              ...group.values.slice(0, action.groupIndex),
              {
                ...group.values[action.groupIndex],
                [action.key]: [
                  ...group.values[action.groupIndex][action.key].slice(0, action.index),
                  ...group.values[action.groupIndex][action.key].slice(action.index + 1),
                ],
              },
              ...group.values.slice(action.groupIndex + 1),
            ],
          },
        },
      };
    } else if (action.groupKey === action.key) {
      const nonGroup = state.dataPoints[action.key] as IDataPoint;
      if (nonGroup.values.length < 2 || nonGroup.values[action.index] === undefined) {
        return state;
      }
      return {
        ...state,
        activeDataPoint: {
          groupIndex: action.groupIndex,
          groupKey: action.groupKey,
          index:
            state.activeDataPoint.index >= action.index
              ? state.activeDataPoint.index - 1
              : state.activeDataPoint.index,
          key: action.key,
        },
        dataPoints: {
          ...state.dataPoints,
          [action.key]: {
            ...nonGroup,
            values: [
              ...nonGroup.values.slice(0, action.index),
              ...nonGroup.values.slice(action.index + 1),
            ],
          },
        },
      };
    }
  }
  return state;
};



