import { IDataPointGroup, IDataPointValue, IState } from "../../types";
import { newDefaultValue } from "../util";

export interface IAddNewGroupAnswerAction {
  groupKey: string;
  type: "ADD NEW GROUP ANSWER";
}

export const addNewGroupAnswer = (state: IState, action: IAddNewGroupAnswerAction): IState => {
  if (action.groupKey in state.dataPoints && state.dataPoints[action.groupKey].isGroup) {
    const group = state.dataPoints[action.groupKey] as IDataPointGroup;
    if (Object.keys(group.dataPoints).length < 1) {
      return state;
    }
    const newValue: { [key: string]: Array<IDataPointValue<any>> } = {};
    for (const dataPointKey of Object.keys(group.dataPoints)) {
      newValue[dataPointKey] = [newDefaultValue(group.dataPoints[dataPointKey].type)];
    }
    return {
      ...state,
      activeDataPoint: {
        groupIndex: group.values.length,
        groupKey: group.key,
        index: 0,
        key: Object.keys(group.dataPoints)[0],
      },
      dataPoints: {
        ...state.dataPoints,
        [action.groupKey]: {
          ...group,
          values: [...group.values, newValue],
        },
      },
    };
  }
  return state;
};



