import { IDataPointGroup, IState } from "../../types";

export interface IRemoveGroupAction {
  groupIndex: number;
  groupKey: string;
  type: "REMOVE GROUP";
}

export const removeGroup = (state: IState, action: IRemoveGroupAction): IState => {
  if (action.groupKey in state.dataPoints && state.dataPoints[action.groupKey].isGroup) {
    const group = state.dataPoints[action.groupKey] as IDataPointGroup;
    if (
      Object.keys(group.dataPoints).length < 1 ||
      group.values.length < 2 ||
      group.values[action.groupIndex] === undefined
    ) {
      return state;
    }
    return {
      ...state,
      activeDataPoint: {
        groupIndex:
          state.activeDataPoint.groupIndex >= action.groupIndex
            ? state.activeDataPoint.groupIndex - 1
            : state.activeDataPoint.groupIndex,
        groupKey: action.groupKey,
        index: 0,
        key: Object.keys(group.dataPoints)[0],
      },
      dataPoints: {
        ...state.dataPoints,
        [action.groupKey]: {
          ...group,
          collapsedValues: group.collapsedValues.filter(v => v !== action.groupIndex),
          values: [
            ...group.values.slice(0, action.groupIndex),
            ...group.values.slice(action.groupIndex + 1),
          ],
        },
      },
    };
  }
  return state;
};



